<template>
  <div class="container">
    <h4>Conteggi Fatture Elettroniche</h4>
    <div class="row">
      <div class="col-sm-4 form-group">
        <label for>Seleziona un aggregatore</label>
        <select v-model="selectedAdmin" class="form-control">
          <option value="null" disabled selected>Seleziona</option>
          <option
            v-for="admin in admins"
            :key="admin.ID"
            :value="admin.ID"
          >{{ admin.Username }} ({{ admin.Count || 0 }} aziende)</option>
        </select>
      </div>
      <div class="col-sm-2 form-group">
        <label for>Seleziona anno</label>
        <select v-model="selectedYear" class="form-control">
          <option value="null" disabled selected>Seleziona</option>
          <option v-for="year in years" :key="year" :value="year">{{ year }}</option>
          <option value="all">Tutti</option>
        </select>
      </div>
    </div>
    <div class="row">
      <div class="col-md-6 form-group">
        <button
          class="btn btn-success col-sm-6"
          @click="startCount(selectedAdmin, selectedYear)"
        >Avvia conteggi</button>
        <p style="margin-top: .5rem;" v-if="selectedAdmin != null">
          <b>Tempo stimato:</b>
          tra i {{Math.ceil((admins.find(x => (x.ID == selectedAdmin)).Count * 0.10)) }} e i {{ Math.ceil((admins.find(x => (x.ID == selectedAdmin)).Count * 0.50)) }} secondi
        </p>
      </div>
    </div>
    <div class="row" style="margin-top: 2rem;" v-if="counts.list.length > 0">
      <div class="col-md-12">
        <h3>Conteggi {{ admins.find(x => (x.ID == counts.admin)).Username }}</h3>
        <p>{{ counts.companies }} aziende elaborate in {{ counts.elapsed }} secondi</p>
        <p>
          <button class="btn btn-primary" @click="downloadCSV()">Scarica CSV</button>
        </p>
        <table class="table table-responsive">
          <thead class="thead-dark">
            <tr>
              <th>IDAzienda</th>
              <th>Denominazione</th>
              <th>Partita IVA</th>
              <th>Codice Fiscale</th>
              <th>Fatture Attive</th>
              <th>Fatture Passive</th>
              <th>Spazio Occupato</th>
              <th>Bytes</th>
            </tr>
          </thead>
          <tbody>
            <tr style="background-color: #ccc; font-weight:bold;">
              <td scope="row" colspan="4">TOTALE ({{ counts.companies }})</td>
              <td>{{ counts.totals.attive }}</td>
              <td>{{ counts.totals.passive }}</td>
              <td>{{ counts.totalsize }}</td>
              <td>{{ counts.totalbytes }}</td>
            </tr>
            <tr v-for="count in counts.list" :key="count.IDAzienda">
              <td scope="row">{{ count.IDAzienda }}</td>
              <td>{{ count.Denominazione }}</td>
              <td>{{ count.PartitaIVA }}</td>
              <td>{{ count.CodiceFiscale }}</td>
              <td>{{ count.FattureAttive }}</td>
              <td>{{ count.FatturePassive }}</td>
              <td>{{ count.SpazioOccupato }}</td>
              <td>{{ count.Bytes }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      admins: [],
      selectedAdmin: null,
      selectedYear: null,
      currentyear: null,
      years: [],
      counts: { list: [] }
    };
  },
  mounted() {
    this.$store.dispatch("loading");
    this.axios
      .get(this.apiURL + "/fe/conteggi/admin-list")
      .then(r => (this.admins = r.data))
      .then(() => {
        this.currentyear = new Date().getFullYear();
        for (var i = this.currentyear; i >= 2017; i--) {
          this.years.push(i);
        }
        this.$store.dispatch("loaded");
      });
  },
  methods: {
    startCount(admin, year) {
      if (admin == null || year == null) {
        alert("Sono necessari sia l'aggregatore che l'anno");
        return false;
      }
      this.$store.dispatch("loading");
      this.axios
        .get(this.apiURL + "/fe/conteggi/conta/" + admin + "/" + year)
        .then(r => (this.counts = r.data))
        .then(() => this.$store.dispatch("loaded"));
    },
    downloadCSV() {
      let headers = [
        "IDAzienda",
        "Denominazione",
        "Partita IVA",
        "Codice Fiscale",
        "Fatture Attive",
        "Fatture Passive",
        "Spazio Occupato",
        "Bytes"
      ];

      let rows = this.counts.list.map(e => [
        e.IDAzienda,
        // Escludo tutti i caratteri che potrebbero causare problemi con l'encoding
        e.Denominazione.replace(/([^aA-zZ0-9\s\-_.!~*'()])/g, ""),
        e.PartitaIVA,
        e.CodiceFiscale,
        e.FattureAttive,
        e.FatturePassive,
        e.SpazioOccupato,
        e.Bytes
      ]);

      // Creazione contenuto CSV (con delimitatore "")
      let csvContent =
        'data:text/csv;charset=utf-8,"' + headers.join('";"') + '"\n';

      csvContent += rows.map(e => '"' + e.join('";"')).join('"\n') + '"';

      csvContent += "\n\n" + '"TOTALE";;;;"' + this.counts.totals.attive + '";"' + this.counts.totals.passive + '";"' + this.counts.totalsize + '";"' + this.counts.totalbytes + '"' +"\n";

      var encodedUri = encodeURI(csvContent);
      var link = document.createElement("a");
      var filename = 'ConteggiFE_' + this.admins.find(x => (x.ID == this.counts.admin)).Username + '_' + this.counts.year + '.csv';
      link.setAttribute("href", encodedUri);
      link.setAttribute("download", filename);
      document.body.appendChild(link); // Required for FF

      link.click();

    }
  }
};
</script>